.title-page{
    font-size: 1.6rem;
    font-weight: 600;
    margin-bottom: 40px;
}
.form-group{
    margin-bottom: 1rem;
    display: flex;
    flex-direction: column;
    width: 50%;
    height: 80px;
    padding: 12px;
    label {
        font-size: 1.2rem;
        margin-bottom: 0.5rem;
        text-align: left; /* Đặt nội dung bên trái */
    }
    input{
        width: 100%;
        padding: 0.5rem;
        font-size: 1rem;
        border: 1px solid #ccc;
        border-radius: 5px;
        &:focus{
            outline: none;
            border: 1px solid #007bff;
        }
    }

    .error{
        color: red;
        font-size: 0.8rem;
    }
    .success{
        color: green;
        font-size: 0.8rem;
    }
    .btn{
        padding: 0.5rem 1rem;
        font-size: 1rem;
        border: none;
        border-radius: 5px;
        background-color: #007bff;
        color: white;
        cursor: pointer;
        margin-top: 1rem;
    }
    .btn:hover{
        background-color: #0056b3;
    }
    .btn:disabled{
        background-color: #ccc;
        cursor: not-allowed;
    }
    .btn:disabled:hover{
        background-color: #ccc;
    }
}